const ContainerImageCard: React.FC<{ image: ContainerImage }> = ({ image }) => (
  <Card 
    isCompact
    style={{
      backgroundColor: 'var(--pf-t--global--background--color--primary--default)',
      boxShadow: 'var(--pf-t--global--box-shadow--md)',
    }}
  >
    <CardHeader>
      <Flex justifyContent={{ default: 'justifyContentSpaceBetween' }} style={{ width: '100%' }}>
        <FlexItem>
          <CubeIcon style={{ color: 'var(--pf-t--global--icon--color--status--danger--default)', fontSize: '1.5rem' }} />
        </FlexItem>
        <FlexItem>
          <Flex gap={{ default: 'gapSm' }}>
            <FlexItem>
              <Button variant="plain" aria-label="Favorite" size="sm">
                <StarIcon style={{ color: image.isFavorite ? 'var(--pf-t--global--color--nonstatus--orange--default)' : undefined }} />
              </Button>
            </FlexItem>
            <FlexItem>
              <Button variant="plain" aria-label="External link" size="sm">
                <ExternalLinkAltIcon />
              </Button>
            </FlexItem>
            <FlexItem>
              <Button variant="plain" aria-label="Download" size="sm">
                <DownloadIcon />
              </Button>
            </FlexItem>
          </Flex>
        </FlexItem>
      </Flex>
    </CardHeader>
    <CardTitle>
      <Content component="h3" style={{ margin: 0 }}>{image.name}</Content>
    </CardTitle>
    <CardBody>
      <Content component="p" style={{ 
        fontSize: 'var(--pf-t--global--font--size--body--sm)',
        color: 'var(--pf-t--global--text--color--subtle)',
        marginBottom: 'var(--pf-t--global--spacer--md)'
      }}>
        {image.description}
      </Content>
      <InputGroup style={{ marginBottom: 'var(--pf-t--global--spacer--sm)' }}>
        <InputGroupItem isFill>
          <TextInput
            readOnlyVariant="default"
            value={image.pullCommand}
            aria-label="Pull command"
          />
        </InputGroupItem>
        <InputGroupItem>
          <Button 
            variant="control" 
            aria-label="Copy to clipboard"
            onClick={() => navigator.clipboard.writeText(image.pullCommand)}
          >
            <CopyIcon />
          </Button>
        </InputGroupItem>
      </InputGroup>
      <Content component="p" style={{ 
        fontSize: 'var(--pf-t--global--font--size--body--sm)',
        color: 'var(--pf-t--global--text--color--subtle)',
        margin: 0
      }}>
        Latest tag: {image.latestTag}
      </Content>
    </CardBody>
    <CardFooter>
      <Button variant="link" isInline icon={<ArrowRightIcon />} iconPosition="end">
        More tags
      </Button>
    </CardFooter>
  </Card>
);

const Dashboard: React.FunctionComponent = () => {
  const [searchValue, setSearchValue] = React.useState('');
  const [isFilterOpen, setIsFilterOpen] = React.useState(false);
  const [selectedFilter, setSelectedFilter] = React.useState<string>('All');
  const [selectedVariant, setSelectedVariant] = React.useState<string>('latest');

  const filterOptions = ['All', 'Languages', 'Databases', 'Tools'];

  return (
    <CompassContent>
      {/* Hero Banner - asymmetric corners per Compass design */}
      <div 
        className="pf-v6-c-hero"
        style={{
          marginBottom: 'var(--pf-t--global--spacer--xl)', /* 32px */
          backgroundColor: 'rgba(199, 199, 199, 0.25)',
          borderStartStartRadius: '24px',
          borderStartEndRadius: '72px',
          borderEndEndRadius: '24px',
          borderEndStartRadius: '72px',
          padding: 'var(--pf-t--global--spacer--xl)',
        }}
      >
        <div style={{ maxWidth: '600px' }}>
          <Content component="h1" style={{ marginBottom: 'var(--pf-t--global--spacer--md)' }}>
            Project Hummingbird Container Images
          </Content>
          <Content component="p" style={{ marginBottom: 'var(--pf-t--global--spacer--lg)' }}>
            Red Hat is excited to introduce <strong>Hummingbird container images</strong>.
            These container images are near-zero CVE, lightweight images ready 
            to plug and play with your existing stack.
          </Content>
          <Flex gap={{ default: 'gapMd' }}>
            <FlexItem>
              <Button variant="primary">Request an image</Button>
            </FlexItem>
            <FlexItem>
              <Button variant="link" icon={<ArrowRightIcon />} iconPosition="end">
                Learn more
              </Button>
            </FlexItem>
          </Flex>
        </div>
      </div>

      {/* Recommended Container Images Header */}
      <div style={{ 
        position: 'sticky',
        top: 0,
        zIndex: 1,
      }}>
        <Flex 
          justifyContent={{ default: 'justifyContentSpaceBetween' }} 
          alignItems={{ default: 'alignItemsCenter' }}
          flexWrap={{ default: 'wrap' }}
          gap={{ default: 'gapMd' }}
        >
          <FlexItem>
            <Content component="h2" style={{ margin: 0 }}>Recommended Container Images</Content>
          </FlexItem>
          <FlexItem>
            <Flex gap={{ default: 'gapMd' }} alignItems={{ default: 'alignItemsCenter' }} flexWrap={{ default: 'wrap' }}>
              <FlexItem>
                <SearchInput
                  placeholder="Search"
                  value={searchValue}
                  onChange={(_event, value) => setSearchValue(value)}
                  onClear={() => setSearchValue('')}
                  style={{ width: '160px' }}
                />
              </FlexItem>
              <FlexItem>
                <Select
                  id="filter-select"
                  isOpen={isFilterOpen}
                  selected={selectedFilter}
                  onSelect={(_event, value) => {
                    setSelectedFilter(value as string);
                    setIsFilterOpen(false);
                  }}
                  onOpenChange={setIsFilterOpen}
                  toggle={(toggleRef: React.Ref<MenuToggleElement>) => (
                    <MenuToggle
                      ref={toggleRef}
                      onClick={() => setIsFilterOpen(!isFilterOpen)}
                      isExpanded={isFilterOpen}
                      style={{ minWidth: '80px' }}
                    >
                      {selectedFilter}
                    </MenuToggle>
                  )}
                >
                  <SelectList>
                    {filterOptions.map((option) => (
                      <SelectOption key={option} value={option}>
                        {option}
                      </SelectOption>
                    ))}
                  </SelectList>
                </Select>
              </FlexItem>
              <FlexItem>
                <ToggleGroup aria-label="Image variant filter">
                  <ToggleGroupItem
                    text="Latest"
                    buttonId="toggle-latest"
                    isSelected={selectedVariant === 'latest'}
                    onChange={() => setSelectedVariant('latest')}
                  />
                  <ToggleGroupItem
                    text="FIPS"
                    buttonId="toggle-fips"
                    isSelected={selectedVariant === 'fips'}
                    onChange={() => setSelectedVariant('fips')}
                  />
                  <ToggleGroupItem
                    text="Builder"
                    buttonId="toggle-builder"
                    isSelected={selectedVariant === 'builder'}
                    onChange={() => setSelectedVariant('builder')}
                  />
                  <ToggleGroupItem
                    text="Go Tools"
                    buttonId="toggle-go-tools"
                    isSelected={selectedVariant === 'go-tools'}
                    onChange={() => setSelectedVariant('go-tools')}
                  />
                </ToggleGroup>
              </FlexItem>
            </Flex>
          </FlexItem>
        </Flex>
      </div>

      {/* Progress indicator */}
      <div style={{ 
        height: '4px', 
        backgroundColor: 'var(--pf-t--global--border--color--default)',
        borderRadius: '2px',
        overflow: 'hidden',
        marginTop: 'var(--pf-t--global--spacer--md)', /* small gap after header */
      }}>
        <div style={{ 
          width: '30%', 
          height: '100%', 
          backgroundColor: 'var(--pf-t--global--color--brand--default)'
        }} />
      </div>

      {/* Recommended Container Images Grid */}
      <Grid hasGutter style={{ marginTop: 'var(--pf-t--global--spacer--md)' /* 16px */ }}>
        {recommendedImages.map((image, index) => (
          <GridItem key={index} span={12} md={4}>
            <ContainerImageCard image={image} />
          </GridItem>
        ))}
      </Grid>

      {/* Languages/Runtimes Section */}
      <Content component="h2" style={{ marginTop: 'var(--pf-t--global--spacer--xl)', marginBottom: 'var(--pf-t--global--spacer--xl)' /* 32px */ }}>
        Languages/Runtimes
      </Content>
      <Grid hasGutter>
        {languageRuntimes.map((image, index) => (
          <GridItem key={index} span={12} md={4}>
            <ContainerImageCard image={image} />
          </GridItem>
        ))}
      </Grid>
    </CompassContent>
  );
};

export { Dashboard };
